.pagecontact{
.formitem{
  @include fz(16);
  &:nth-of-type(n+2){
    margin-top: 20px;
  }
  &-input{
    width: 100%;
    border: 1px solid #ccc;
    margin-top: 5px;
    padding: 3px;
  }
  &-name{
    font-weight: bold;
  }
  &-textarea{
    border: 1px solid #ccc;
    width: 100%;
    padding: 3px;
  }
  &-required{
    color: $rColor;
  }

}
&-btn{
  @include _btn;
  @include fz(14);
  background-color: black;
  margin: 20px auto 0;
  display: block;
}
}